@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Poppins&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

:root {
  --primary-color: #070707;
  --secondary-color: #ececec;
  --tersier-color: #ffc06d;
  --warning-color: #ff5858;
  --acceppt-color: #67ff67;
}

a {
  text-decoration: none;
  color: inherit;
}

.btn {
  cursor: pointer;
}

body {
  margin: 0;
  background: url(/pokemon-app/static/media/wallpaper.d95635ba.jpg);
  background-color: #535353;
  background-blend-mode: multiply;
  background-size: 100vw;
  background-repeat: repeat-y;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}

main {
  padding-top: 40px;
  min-height: calc(100vh - 60px);
}


header {
  background-color: #070707;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  color: var(--secondary-color);
}

header h3 {
  color: var(--primary-color);
}

nav {
  display: flex;
}

.nav-list {
  padding: 0.25em 0.5em;
  margin-right: 0.5em;
  transition: all 0.5s ease-in-out;
  border-radius: 0.5em;
}

.nav-list:hover {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.icon {
  display: flex;
  align-items: center;
}

.appName {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 24px;
  color: white;
}

.backButton {
  font-family: "Poppins", sans-serif;
  margin-right: 10px;
  background: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Burger Button */
.container {
  display: inline-block;
  cursor: pointer;
}

.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: var(--secondary-color);
  margin: 6px 0;
  border-radius: 4px;

  transition: 0.4s;
}

.change .bar1 {
  transform: rotate(-45deg) translate(-9px, 6px);
  border-radius: 4px;
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: rotate(45deg) translate(-8px, -8px);
  border-radius: 4px;
}

@media only screen and (min-width: 640px) {
  .container {
    display: none;
  }
}

@media only screen and (max-width: 640px) {
  nav {
    display: flex;
    flex-direction: column;
    width: 100vw;
    position: absolute;
    top: 60px;
    left: 0;
    background-color: var(--secondary-color);
  }

  .nav-list {
    width: 100vw;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-radius: 0;
    padding: 0.5em;
  }

  nav.active {
    display: none;
  }

  .nav-list:hover {
    background-color: var(--primary-color);
    color: var(--secondary-color);
  }
}

.detail-box {
  display: block;
  width: 100%;
  margin: auto;
}

.detail-pokeball:nth-child(2n) {
  width: 100px;
  height: 100px;
  content: "";
  position: absolute;
  top: 50px;
  right: 100px;
  background-size: cover;
  opacity: 20%;
}

.detail-pokeball:nth-child(2n + 1) {
  width: 100px;
  height: 100px;
  content: "";
  position: absolute;
  bottom: 75px;
  left: 150px;
  background-size: cover;
  opacity: 15%;
}

.detail-box h2 {
  color: var(--secondary-color);
}

@media only screen and (min-width: 768px) {
  .detail-box {
    width: 80%;
  }
}

.detail-content-box {
  display: block;
  margin: auto;
  width: 100%;
  background: var(--secondary-color);
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  padding: 20px 10px;
  min-height: calc(100vh - 192px - 20vh);
  text-transform: capitalize;
  position: relative;
}

.detail-info-box {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-wrap: wrap;
}

.detail-content {
  width: min(100%, 400px);
  margin-bottom: 20px;
}

.detail-content-box .divider {
  width: min(50%, 320px);
  display: block;
  margin: auto;
  margin-top: -10px;
  margin-bottom: 20px;
  height: 4px;
  border-radius: 4px;
  background: grey;
}

.detail-header {
  height: 80px;
  width: 100%;
  padding: 5px 20px;
}

.pokemon-name {
  text-transform: capitalize;
}

.type-box {
  display: flex;
}

.type-box * {
  margin-right: 0.5em;
}

.alert {
  width: min(80%, 300px);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--secondary-color);
  padding: 20px 20px;
  border-radius: 20px;
  z-index: 100;
  box-shadow: #64646f33 0px 7px 29px 0px;
}

.alert .btn-resubmit {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.25em 1em;
  border-radius: 1em;
  border: none;
  outline: none;
  background-color: var(--warning-color);
  display: block;
  margin: auto;
  margin-top: 10px;
  color: var(--secondary-color);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 5px 0px, rgba(0, 0, 0, 0.2) 0px 0px 1px 0px;
}

.form-nickname-input h4 {
  margin-bottom: 15px;
  text-align: center;
}

input#nickname {
  display: block;
  outline: none;
  border: none;
  border-radius: 2em;
  width: 100%;
  padding: 0.5em 2em;
  background-color: rgb(211, 211, 211);
  margin-bottom: 0.5em;
}

.btn-catch {
  padding: 0.25em 1em;
  height: 35px;
  border-radius: 1em;
  border: none;
  outline: none;
  background-color: var(--secondary-color);
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  color: var(--primary-color);
  margin-bottom: 10px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 5px 0px, rgba(0, 0, 0, 0.2) 0px 0px 1px 0px;
}

.detail-img {
  display: block;
  margin: auto;
  width: 20vh;
}

.detail-img img {
  width: 20vh;
  aspect-ratio: 1/1;
}

.button-group {
  display: flex;
  font-size: 12px;
}

.button-group .btn {
  padding: 0.25em 1em;
  border-radius: 1em;
  border: none;
  outline: none;
  background-color: var(--warning-color);
  display: block;
  margin: auto;
  color: var(--secondary-color);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 5px 0px, rgba(0, 0, 0, 0.2) 0px 0px 1px 0px;
}

#submit {
  font-size: 12px;
  padding: 0.25em 1em;
  border-radius: 1em;
  border: none;
  outline: none;
  background-color: var(--tersier-color);
  display: block;
  margin: auto;
}

.move-content-box {
  width: 100%;
}
.move-content-box h2 {
  text-align: center;
  margin-bottom: 10px;
}

.move-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.move-box {
  display: inline-block;
  width: 150px;
  padding: 5px;
  text-align: center;
  border: solid 1px #b8b8b8;
  text-transform: capitalize;
}

.table {
  width: 100%;
}

.table-column {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.table-row {
  padding: 0.25em 0em;
}

.table-row.title {
  font-weight: 500;
  width: max(30%, 100px);
  text-align: left;
}

.table-row.content {
  width: max(70%, 100px);
  text-align: left;
}

.type-label {
  padding: 0.25em 0.5em;
  border-radius: 1em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: 500;
  color: var(--secondary-color);
  font-size: 10px;
  letter-spacing: 0.1em;
}

.type-label.normal {
  background: #a8a77a;
}

.type-label.fire {
  background: #ee8130;
}

.type-label.water {
  background: #6390f0;
}

.type-label.electric {
  background: #f7d02c;
}

.type-label.grass {
  background: #7ac74c;
}

.type-label.ice {
  background: #96d9d6;
}

.type-label.fighting {
  background: #c22e28;
}

.type-label.poison {
  background: #a33ea1;
}

.type-label.ground {
  background: #e2bf65;
}

.type-label.flying {
  background: #a98ff3;
}

.type-label.psychic {
  background: #f95587;
}

.type-label.bug {
  background: #a6b91a;
}

.type-label.rock {
  background: #b6a136;
}

.type-label.ghost {
  background: #735797;
}

.type-label.dragon {
  background: #6f35fc;
}

.type-label.dark {
  background: #705746;
}

.type-label.steel {
  background: #b7b7ce;
}

.type-label.fairy {
  background: #d685ad;
}

.chart-bar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.chart-bar-row {
  padding: 0.25em 0em;
}

.chart-bar-row.title {
  font-weight: 500;
  width: max(30%, 100px);
  text-align: left;
}

.chart-bar-row.content {
  position: relative;
  width: 160px;
  height: 2px;
  border-radius: 4px;
  background-color: #dbdbdb;
  overflow: hidden;
}

.chart-bar-row.content .line-bar {
  padding: 0.25em 0.5em;
  border-radius: 4px;
  height: 2px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.pokedex-box {
  display: block;
  margin: auto;
  padding-bottom: 20px;
}

.pokedex-box h2,
.pokedex-box small {
  color: var(--secondary-color);
}

.pokedex-box-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.pokedex-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.pokemon-box {
  display: inline-block;
  width: max(25%, 320px);
  padding: 0px 20px;
}

.info-inventory {
  width: 100%;
  padding: 0px 20px;
}

.info-inventory p {
  text-align: right;
  margin-bottom: 5px;
}

.pokemon-card {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 0.5em 0.5em;
  width: max(25%, 280px);
  background-color: var(--secondary-color);
  border-radius: 3em;
}

.pokemon-card .image-box {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--tersier-color);
  margin-right: 1em;
}

.pokemon-card .image-box img {
  display: block;
  width: inherit;
  aspect-ratio: 1/1;
}

.pokemon-card .pokemon-name {
  letter-spacing: 0.25px;
}

.pokedex-info {
  width: calc(100% - 100px);
}

.go {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  width: 30px;
  height: 30px;
  display: flex;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  right: 0.5em;
}

.pokemon-card::after {
  width: 100%;
  height: 100%;
  position: absolute;
  content: "";
  background: transparent;
  top: 0;
  left: 0;
}

.btn-group {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 280px;
}

.btn-pagination {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: var(--secondary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.mypokedex-box {
  display: flex;
  justify-content: center;
  flex-flow: column;
  padding: 0 20px;
}

.mypokedex-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.mypokedex-box h2 {
  text-align: center;
  margin-bottom: 20px;
}

.mypokedex-card-box {
  display: inline-block;
  position: relative;
  background: var(--secondary-color);
  width: max(20%, 320px);
  padding: 0.5em 0.5em;
  border-radius: 60px;
  margin: 0 5px 20px 5px;
  box-shadow: #56565c2c 0px 7px 29px 0px;
  border: solid 0.1px rgba(0, 0, 0, 0.418);
}

.mypokedex-card {
  display: flex;
  align-items: center;
}

.mypokedex-img {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  overflow: hidden;
  background: #ffe7bb;
  margin-right: 10px;
}

.mypokedex-img img {
  width: 60px;
  height: 60px;
}

.mypokedex-card .mypokedex-info {
  width: calc(100% - 150px);
}

.mypokedex-card .btn-release {
  padding: 0.25em 0.5em;
  background: #ffe7bb;
  border-radius: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  cursor: pointer;
}

.pokeball {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 40%;
  right: 15%;
  background-size: cover;
  opacity: 10%;
  transform: rotate(25deg);
}

